.section-3

  .items

    &__list
      display: flex
      justify-content: space-between
      flex-wrap: wrap
      +r(768)
        justify-content: space-around

    &__el
      width: 250px
      margin-bottom: 35px

  .item
    display: flex
    flex-direction: column
    text-align: center
    font-size: 15px

    &__top
      width: 100%
      height: 250px
      margin-bottom: 10px
      border-radius: 10%
      -webkit-filter: brightness(70%)
      opacity: .8
      transition: all .2s ease
      cursor: pointer
      &:hover
        opacity: 1
        -webkit-filter: brightness(100%)
        transform: scale(1.3)
        z-index: 5

    &__bottom

      h3
        font-size: 20px

      span
        letter-spacing: 1.1px

    &_1
      .item__top
        background: url("../img/cake1.jpg") no-repeat center left
        background-size: cover

